import { Disclosure } from "@headlessui/react";

function classNames(...classes) {
  return classes.filter(Boolean).join(" ");
}

export default function TopMenu(props) {
  return (
    <Disclosure as="nav">
      {({ open }) => (
        <>
          <div className="mt-[-30px] py-2 mx-auto max-w-7xl px-2 bg-primary rounded-full">
            <div className="flex flex-1 space-x-4 items-center sm:items-stretch sm:justify-start">
              {props.data.map((item) => (
                <a
                  key={item.name}
                  href={item.href}
                  className={classNames(
                    item.current
                      ? "bg-primarydeep text-white"
                      : "text-white/60  hover:text-white",
                    "rounded-full px-8 py-4 text-lg font-mediumFont"
                  )}
                  aria-current={item.current ? "page" : undefined}
                >
                  {item.name}
                </a>
              ))}
            </div>
          </div>
        </>
      )}
    </Disclosure>
  );
}
