import { HashRouter, Routes, Route } from "react-router-dom";

import React, { useEffect } from "react";
import AOS from "aos";
import "aos/dist/aos.css";
import NotfoundScreen from "./pages/NotfoundScreen";
import HomeScreen from "./pages/HomeScreen";
import FundDetailScreen from "./pages/FundingDetailScreen";
import SubScreen from "./pages/SubScreen";
import CampainDetailScreen from "./pages/CampainScreen";

export default function App() {
  useEffect(() => {
    AOS.init({});
  });

  return (
    <HashRouter>
      <Routes basename={process.env.PUBLIC_URL}>
        <Route exact path="/" element={<HomeScreen />} />
        <Route path="/home" element={<HomeScreen />} />
        <Route path="home:id" element={<HomeScreen />} />
        <Route path="/fundDetail" element={<FundDetailScreen />} />
        <Route path="/subPage" element={<SubScreen />} />
        <Route path="/campainDetail" element={<CampainDetailScreen />} />
        <Route path="/*" element={<NotfoundScreen />} />
      </Routes>
    </HashRouter>
  );
}
