import { useState } from "react";
import fundingListData from "../../data/fundingListData";

function classNames(...classes) {
  return classes.filter(Boolean).join(" ");
}

const totalItemCount = fundingListData.length;

const categoryButtons = [
  { id: "all", label: "" },
  { id: "Proceeding", label: " " },
  { id: "closed", label: "" },
];

//     
const categoryCounts = categoryButtons.reduce((counts, category) => {
  const count = fundingListData.filter(
    (feature) => feature.category === category.id
  ).length;
  counts[category.id] = count;
  return counts;
}, {});

export default function FundingList() {
  const [activeCategory, setActiveCategory] = useState("all");

  const filteredData =
    activeCategory === "all"
      ? fundingListData
      : fundingListData.filter(
          (feature) => feature.category === activeCategory
        );

  return (
    <div className="lg:max-w-7xl mx-auto px-6">
      <div className="mx-auto mt-40 lg:flex ">
        <div className="min-w-0 flex-1">
          <h2 className="text-4xl font-midiumFont text-gray-900 sm:truncate sm:tracking-tight">
             
          </h2>
          <div className="mt-1 flex flex-col sm:mt-0 sm:flex-row sm:flex-wrap sm:space-x-6">
            <div className="mt-2 flex items-center text-base text-gray-500">
                !   .
            </div>
          </div>
        </div>
        <div className="mt-5 flex m-2 lg:mt-0">
          {categoryButtons.map((button) => (
            <span key={button.id} className="ml-3 ">
              <button
                onClick={() => setActiveCategory(button.id)}
                type="button"
                className={classNames(
                  button.id === activeCategory
                    ? "bg-primary text-white"
                    : "bg-white text-gray-900 shadow-sm hover:bg-gray-50 ring-1 ring-inset ring-gray-300",
                  "rounded-2xl px-3 py-2 text-sm  "
                )}
              >
                {button.label} (
                {button.id === "all"
                  ? totalItemCount
                  : categoryCounts[button.id]}
                )
              </button>
            </span>
          ))}
        </div>
      </div>
      <div className="mt-14 2xl:mb-4">
        <div className="grid xl:grid-cols-2 md:grid-cols-1 gap-6">
          {filteredData.map((feature, index) => (
            <a href="#/fundDetail">
              <div className="bg-white shadow rounded-2xl  outline outline-1 outline-gray-200">
                <div className="flex items-center gap-6 ">
                  <img
                    src={`images/funding/${feature.image}`}
                    alt={feature.title}
                    className="rounded-tl-2xl rounded-bl-2xl h-60"
                  />
                  <div className="mr-5">
                    <h2 className="text-xl mb-1 break-keep">{feature.title}</h2>
                    <p className="font-midiumFont text-sm text-gray-500">
                       : {feature.name}
                    </p>
                    <div className="flex items-center gap-3 mt-4">
                      <div
                        className="py-[2px] bg-primary rounded-2xl  shadow-lg hidden sm:block"
                        style={{ width: `${feature.level}%` }}
                      ></div>
                      <h2 className="text-xl mb-4 font-midiumFont tracking-tight text-gray-900 sm:text-xl">
                        {feature.level}%
                      </h2>
                    </div>
                  </div>
                </div>
              </div>
            </a>
          ))}
        </div>
      </div>
    </div>
  );
}
